import React from 'react'
 import { Link } from 'react-router-dom';
import Haldiram from '../assets/images/haldirams.png';
import dinein from '../assets/images/dine-in.png';
 
export const HomeTitle = () => {
  
  return (
    <>
      <div className="scan-qr-code container-fluid px-0 align-items-center d-flex justify-content-center position-relative">
        <div className="row d-inline text-center">
          <div className="text-center pb-4 ">
            <img src={Haldiram} alt="haldirams img" />
            <ul className=" d-flex justify-content-center pt-3 ps-0">
              <li className="pe-4 list-unstyled"> <span><i className="fas fa-map-marker-alt text-danger px-1"></i></span> 54/01
                XYZ Street</li>
              <li className="ms-3"><span><i className="fas fa-star text-danger"></i></span>4.5</li>
            </ul>
          </div>

          <div className="d-flex justify-content-between col-md-12 mx-auto pt-4">

            <div className="delivery-box mx-3">
              <Link to="/product" >

                <div className="w-100">
                  <img src={dinein} alt="dine-in" />
                </div>
                <div className="d-flex align-items-center pt-3">
                  <p className="mb-0">Dine In </p>
                  <span className="px-2"><i className="fas fa-long-arrow-alt-right"></i></span>
                </div>

              </Link>

            </div>
          </div>
        </div>
      </div>
    </>
  )
}
