let initialState ={
    popularPPPP :[],
}
const mReducer =(state=initialState ,action)=>{
    if(action.type==="SET_PPOPULAR_PRODUCT"){
        return{
            ...state,
            popularPPPP :action.product
        }
    }
    else{
        return{
            ...state,
        }
    }
}
export default mReducer;