import * as actionTypes from "../constants/shopping-types";
 
const initialState = {

  products:[],
  popularProducts:[],
  cusine:[],
  cart: [], //{id,title,descr,price,img,qty}
  currentItem: null,

};

export const productReducer = (state =  initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_PRODUCTS:
      return {
        
        ...state,
        products:action.payload,
      };

      case actionTypes.SET_CUSINE:
        return{
          ...state,
          cusine:action.payload,
        }

    case actionTypes.SET_POPULAR_PRODUCTS:
      return{
        ...state,
        popularProducts: action.payload,
      }
    case actionTypes.ADD_TO_CART:
      //Get the items data from the products array
      const item = state.products.find((prod) => prod.id === action.payload.id);

      //Check if Item is in cart already
      const inCart = state.cart.find((item) =>
        item.id === action.payload.id ? true : false
      );
      return {
        ...state,
        cart: inCart
          ? state.cart.map((item) =>
              item.id === action.payload.id
                ? { ...item, qty: item.qty + 1 }
                : item
            )
          : [...state.cart, { ...item, qty: 1 }],
      };

    case actionTypes.REMOVE_FROM_CART:
      return {
        ...state,
        cart: state.cart.filter((item) => item.id !== action.payload.id),
      };
    case actionTypes.ADJUST_QTY:
      return {
        ...state,
        cart: state.cart.map((item) =>
          item.id === action.payload.id
            ? { ...item, qty: action.payload.qty }
            : item
        ),
      };
    case actionTypes.LOAD_CURRENT_ITEM:
      return {
        ...state,
        currentItem: action.payload,
      };

// save order product
    case actionTypes.SET_ORDER_PRODUCT:
      return{
        ...state,
        orderProduct :action.payload,
      }
      case actionTypes.SET_USER_DETAILS:
        return{
          ...state,
          userDetails :action.payload,
        }
      
    case actionTypes.CREATE_ORDER:
      return{
        ...state,
        order : action.payload
      }

    case actionTypes.CLEAR_ORDER:
      return{
        ...state,
         order :[],
        }
  
    case actionTypes.CLEAR_CART:
      return{
        ...state,
        cart :[],
      }
       
    default:
      return state;
  }
};

 