import React, { useState, useEffect } from "react";
 import { Link  } from "react-router-dom";
  import { useSelector, useDispatch } from "react-redux";
 import { Formik, Form, Field, ErrorMessage } from "formik";
import { useHistory } from "react-router-dom";
import { saveUserDetails } from "../services/actions/productAction";
 
export const FillDetails = () => {
   
  const coupan_name = "big shell";
  const coupan_discount =15;

  
  const [totalPrice, setTotalPrice] = useState(0);
  
  let history = useHistory();
  const cart = useSelector((state) => state.allProducts.cart);
  const dispatch = useDispatch();
  const goToPreviousPath = () => {
    history.goBack();
  };
  
  useEffect(() => {
    let price = 0;
    cart.forEach((item) => {
      price += item.qty * item.price;
    });
    setTotalPrice(price);
  }, [cart, totalPrice, setTotalPrice]);
  const myStyle = {
    backgroundColor: "#149E33",
  };
  return (
    <>
      <main className="page-wrapper">
        <section className="py-4">
          <div className="container fill-details">
            <div className="row">
              <div>
                <Link onClick={goToPreviousPath} className="text-decoration-none text-dark">
                  <h5>
                  <i className="fas fa-chevron-left pe-2"></i>
                    Fill Details
                  </h5>
                </Link>
              </div>
              <div className="col-lg-5 mx-auto pt-5">
                <Formik
                  initialValues={{
                    firstName: "",
                    lastName: "",
                    phone: "",
                    email: "",

                  }}
                  validate={(values) => {
                    const errors = {};
                    if (!values.firstName) {
                      errors.firstName = "Required";
                    }
                    if (!values.phone) {
                      errors.phone = "Required";
                    }
                    if (!values.email) {
                      errors.email = "Required";
                    } else if (
                      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(
                        values.email
                      )
                    ) {
                      errors.email = "Invalid email";
                    }
                    return errors;
                  }}
                  onSubmit={(values, { setSubmitting }) => {
                    let userDetail = {
                      name: values.firstName + " " + values.lastName,
                      phone: values.phone,
                      email: values.email,
                      total_amount: totalPrice,
                      restaurant: 1,
                      coupan_name :coupan_name,
                      coupan_discount :coupan_discount,
                      // address: location,
                      // tableNo: tableNo,
                    };
                    dispatch(saveUserDetails(userDetail));
                    history.push("/payment");
                  }}
                >
                  {({ isSubmitting, values, handleChange, handleSubmit }) => (
                    <Form onSubmit={handleSubmit}>
                      <div className="row pt-5">
                        <div className="mb-3 col-6">
                          <Field
                            type="text"
                            className="form-control"
                            placeholder="First Name*"
                            name="firstName"
                            value={values.firstName}
                            onChange={handleChange}
                            id="exampleInputEmail1"
                            aria-describedby="emailHelp"
                          />
                          <ErrorMessage
                            name="firstName"
                            component="div"
                            className="text-danger"
                          />
                        </div>
                        <div className="mb-3 col-6">
                          <Field
                            type="text"
                            className="form-control"
                            placeholder="Last Name"
                            name="lastName"
                            value={values.lastName}
                            onChange={handleChange}
                            id="exampleInputEmail1"
                            aria-describedby="emailHelp"
                          />
                        </div>
                        <div className="mb-3">
                          <Field
                            type="number"
                            placeholder="Contact Number*"
                            name="phone"
                            value={values.phone}
                            onChange={handleChange}
                            className="form-control"
                            id="exampleInputPassword1"
                          />
                          <ErrorMessage
                            name="phone"
                            component="div"
                            className="text-danger"
                          />
                        </div>
                        <div className="mb-3">
                          <Field
                            type="email"
                            placeholder="Email ID*"
                            name="email"
                            value={values.email}
                            onChange={handleChange}
                            className="form-control"
                            id="exampleInputPassword1"
                          />
                          <ErrorMessage
                            name="email"
                            component="div"
                            className="text-danger"
                          />
                        </div>
                        <div className="pt-3">
                          <button
                            type="submit"
                            className="btn btn-success w-100"
                            style={myStyle}
                            // onClick={handleSubmit}
                            disabled={isSubmitting}
                          >
                            Place Order
                          </button>
                        </div>
                      </div>
                    </Form>
                  )}
                </Formik>
              </div>
            </div>
          </div>
        </section>
        <div className="container d-md-none d-block">
          <div className="row">
            <div className="col-md-5 mx-auto pt-5">
              <div className="d-flex justify-content-center">
                <ul className="list-unstyled d-flex dots">
                  <li className="dots-place-order"></li>
                  <li className="dots-place-order active"></li>
                  <li className="dots-place-order"></li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </main>
    </>
  );
};
 