import React , {useState} from 'react'
import { connect } from 'react-redux';
import { removeFromCart ,adjustQty } from './../services/actions/productAction';

 const CartItem = ({ itemData  ,removeFromCart,adjustQty}) => {

    var [changeItem ,setChangeItem] = useState(itemData.qty);
    const incrementValue =()=>{
        changeItem +=1;
        setChangeItem(changeItem)
        adjustQty(itemData.id ,changeItem);
        
    }

    const decrementValue =()=>{
       if(changeItem>=1){
           changeItem -=1;
           setChangeItem(changeItem);
           adjustQty(itemData.id , changeItem);
       }
    }
    const onChangeHandeller =(e)=>{
        console.log("event are ",e);
        setChangeItem(e.target.value);
        adjustQty(itemData.id ,e.target.value);
        

    }
    return (
        <>
            <div className="shadow-sm p-3">
                <div className="d-xs-flex justify-content-between align-items-center" >
                    <div>
                        <h4 className="pro-text">{(itemData.name).substring(0,16)}</h4>
                        <p className="pro-price">₹ {itemData.price}</p>
                    </div>
                    <div className="d-flex justify-content-start align-items-center">
                    <div onClick={() => { removeFromCart(itemData.id) }} className="text-danger mx-2 my-2 h5">
                        <i className="fas fa-trash" style={{cursor:"pointer"}}>

                        </i>
                    </div>
                    <div>


                        <div className="control product_qty" >

                            <input type="button" className="btn bttn-left btn-outline-light border-end-0" onClick={decrementValue} value="-" />
                            <input type="text" className="input  border border-start-0 border-end-0" name="quantity" value={changeItem}
                            onChange={onChangeHandeller}
                            maxLength="2" max="10" size="1" id="number" />
                            <div onClick={()=>{}} className="d-inline-flex">
                                <input type="button" className="btn bttn-right btn-outline-light border-start-0" onClick={incrementValue} value="+" />
                            </div>
                        </div>
                    </div>
                    </div>
                </div>
            </div>
        </>
    )
}

const mapDispatchToProps = dispatch =>{
    return{
        removeFromCart :(id)=>dispatch(removeFromCart(id)),
        adjustQty :(id , value)=>dispatch(adjustQty(id,value))
    }
}
export default connect( null, mapDispatchToProps)(CartItem);
