import React, { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux';
import Slider from "react-slick";
import { setPopularProducts } from '../../services/actions/productAction';
import axios from 'axios';

export const PopularDishes = () => {
  const popular_Dishes = useSelector((state) => state.allProducts.popularProducts);
  const dispatch = useDispatch();
  const url = "http://139.59.13.212:1267/products/all/?resturantid=1";

  useEffect(() => {
    async function fetchPopularProducts() {
      const response = await axios.get(url);
      dispatch(setPopularProducts(response.data.data.popular_product));
    }
    
    fetchPopularProducts();
  }, [dispatch]);

  //console.log("popular product ", popular_Dishes);

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 7,
    slidesToScroll: 4,
    autoplay: true,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 10,
          slidesToScroll: 4,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 960,
        settings: {
          slidesToShow: 6,
          slidesToScroll: 4,
          infinite: true,
          dots: false,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 5,
          slidesToScroll: 4,
          initialSlide: 2,
          dots: false,
        },
      },
      {
        breakpoint: 480,
        settings: {
          dots: false,
          slidesToShow: 4,
          slidesToScroll: 3,
        },
      },
    ],
  };
  return (
    <>
      <section className="our-popular-dishes pt-2 pt-md-5">
        <div className="container px-0">
          <div className="title-second pb-4 fw-bold">
            Our Popular Dishes
          </div>
          <div className="popular-dishes-slide ">
            <Slider {...settings}>
              {popular_Dishes?.map((product ,index )=> {
                return (
                  <div className="dishes text-center" key={index}>
                    <img src={product.image}
                      style={{ height: '70px', width: "70px", borderRadius: "50%" }}
                      alt="dishe-1" />
                    <div className="dishes-name pt-2">
                      {product.name.substring(0,19)}
                    </div>
                  </div>
                )
              })}
            </Slider>
          </div>
        </div>
      </section>


    </>
  );
}