import React ,{useState} from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMinus } from "@fortawesome/free-solid-svg-icons";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import { connect } from 'react-redux';
import { adjustQty } from './../../services/actions/productAction';

 const IncrementDecrement = ({id , adjustQty}) => {
    
    let [val, updateVal] = useState(1);
     const increment = ()=>{
      val+=1;
      updateVal(val)
      adjustQty(id ,val)
     // console.log("Adjust" , adjustQty(id,val))
  }
  const decrement = ()=>{
      if(val>=1){
        val-=1;
        updateVal(val);
      adjustQty(id ,val);
      }  
  }

    return (
        <>
          <div className="pe-2">
        <div className="control product_qty">
          <div>
            <button className="btn bttn-left btn-outline-light border-end-0 btn-minus" onClick={decrement}>
              <span className="text-secondary" >
                 {/* <i className="fas fa-minus"></i>  */}
                 <FontAwesomeIcon icon={faMinus} className="text-secondary" />
                 </span>
            </button>
          </div>
          <input
            type="number"  className="input border border-start-0 border-end-0 input"
            value={val}
            onChange={() => {}}
          />

          <div>
            <button className="btn bttn-right btn-outline-light border-start-0 btn-plus" onClick={increment}>
              <span className="text-secondary">
                 {/* <i className="fas fa-plus"></i> */}
                 <FontAwesomeIcon icon={faPlus} className="text-secondary" />
              </span>
            </button>
          </div>
        </div>
      </div>  
        </>
    )
}


const mapDispatchToProps = dispatch => {
  return{
      adjustQty: (id,value)=> dispatch(adjustQty(id,value))
  }
}

export default connect(null , mapDispatchToProps)(IncrementDecrement);