import React from 'react'
import filter from '../../assets/images/filter.png'
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import axios from 'axios';
import { setProducts } from './../../services/actions/productAction';

export const Search = () => {
   const dispatch = useDispatch();

   const products = useSelector((state) => state.allProducts.products);

   const searchProduct = async (e) => {
      //console.log("Search is  ",e.target.value);
      if (e.target.value.length > 2) {
         let response = await axios.get(`http://139.59.13.212:1267/search/1/?search=${e.target.value}`);
         let newproduct = response.data.data.filter((value) => {
            return value.name.toLowerCase().includes(e.target.value.toLowerCase());
         });
         console.log("net product ", newproduct);
         dispatch(setProducts(newproduct));
       }
      else {
         const response = await axios.get(`http://139.59.13.212:1267/search/1/?search=`);
         //dispatch(response.data.data.product)
         const allproduct = response.data.data.filter((value) => {
            return value.name.toLowerCase().includes(e.target.value.toLowerCase());
         });
         console.log(allproduct);
         dispatch(setProducts(allproduct))
      }

   } 
   return (
      <>
         <div className="col-md-8 mx-auto px-0">
            <div className="search-box pt-md-4">
               <div className="input-group mb-3">
                  <span className="input-group-text search-icon" id="basic-addon1"><i
                     className="fas fa-search"></i></span>
                  <input type="text" className="form-control" placeholder="What you want to have..."
                     aria-label="Username" aria-describedby="basic-addon1" onChange={searchProduct} />
                  <span className="input-group-text ms-2"><img src={filter} alt="filter" /></span>
               </div>
            </div>
         </div>

      </>
   )
}
