import { React  } from 'react'
import { useSelector ,useDispatch} from 'react-redux';
import { useEffect } from 'react';
import axios from 'axios';
import { Item } from './Item';
import { setProducts } from './../../../services/actions/productAction';

export const ProductItem = ({ productData }) => {
   const dataInsideCusion = useSelector((state) => state.allProducts.products);
    const dispatch = useDispatch();
  const url = "http://139.59.13.212:1267/products/all/?resturantid=1";

  useEffect(() => {
    async function fetchPopularProducts() {
      const response = await axios.get(url);
      //console.log("Cusion Response is ",response.data.data.product);
      dispatch(setProducts(response.data.data.product));
    }
    fetchPopularProducts();
  }, [dispatch]);

   
   //console.log("Product data ", dataInsideCusion);
   return (
      <>
         <div className="accordion-body px-0">
            {dataInsideCusion.map((data) => {
               if (productData.id === data.cuisine_id)
                  return (
                     <Item 
                     key={data.id}
                     img={data.image}
                        name={data.name}
                        price={data.price}
                        id={data.id}
                        desc={data.description}
                        itemData={data} 
                        ></Item> 
                  )
            })}
         </div>

      </>
   )
}
