import React, { useState, useEffect } from 'react'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import upi from '../assets/images/upi.png';
import axios from 'axios';
import { useDispatch } from 'react-redux';
import { clearCart, createOrder } from './../services/actions/productAction';
export const Payments = () => {

    const userDetails = useSelector((state) => state.allProducts.userDetails);
    const orderProducts = useSelector((state) => state.allProducts.orderProduct);
    const [choice, setChoice] = useState('CASH');
    const [order_id, setorder_id] = useState("");
    const dispatch = useDispatch();
    //let order_id;
 

  //  console.log("Final order product", orderProducts)
   // console.log("final userdetails ", userDetails)

    let order = { orderProducts, userDetails }
   // console.log("order is ", order);

    const choiceHandle = (e) => {
        console.log("choice is are ", e.target.attributes.value);
        setChoice(e.target.attributes.value);
        

    }

    const handleConfirm = (e) => {
        dispatch(clearCart())
        dispatch(createOrder(orderProducts, userDetails));

        axios.post("http://139.59.13.212:1267/orders/placeorder/", order)
            .then((response) => {
                console.log("payment ", response.data.data.orderId);
                setorder_id(response.data.data.orderId);

            })
            .catch((error) => {
                console.log(error);
            });
    }
    useEffect(() => {
        if (choice === "") {
            userDetails.payment_mode = "CASH";
        }
        else {
            userDetails.payment_mode = "ONLINE";
        }
    }, [choice, userDetails]);



    return (
        <>
            <main className="page-wrapper">
                <section className="py-4">
                    <div className="container fill-details px-0">
                        <div className="row">
                            <div>
                                <Link to="/product" 
                                    className="text-decoration-none text-dark">
                                        <h5><i className="fas fa-chevron-left pe-2"></i>Select Payment Method</h5>
                                   
                                </Link>
                            </div>
                            <div className="col-lg-5 mx-auto pt-5">
                                <div className="">
                                    <div className="form-check common-radio  ps-0">
                                        <input className="common-radio-input" type="radio" value="ONLINE" name="typeuser" id="upi" hidden="" checked
                                            onChange={choiceHandle}
                                        />
                                        <label className="common-radio-label pl-sm-4 text-dark" for="upi">
                                            <div>
                                                <img className="payment-icon" src={upi} alt="upi" />
                                                Online
                                            </div>
                                        </label>
                                    </div>
                                    <div className="form-check common-radio  ps-0">
                                        <input className="common-radio-input" type="radio" value="CASH" name="typeuser" id="credit-card" hidden=""
                                            onChange={choiceHandle}
                                        />
                                        <label className="common-radio-label pl-sm-4 text-dark" htmlFor="credit-card"><img className="payment-icon"
                                            src={upi} alt="upi" /> Cash</label>
                                    </div>
                                    {/* <div className="form-check common-radio  ps-0 ">
                                        <input className="common-radio-input" type="radio" name="typeuser" id="debit-card" hidden="" />
                                        <label className="common-radio-label pl-sm-4 text-dark" for="debit-card">
                                            <img className="payment-icon"
                                                src= {upi} alt="upi" /> Debit Card</label>
                                    </div> */}
                                </div>
                                <div className="pt-3" onClick={handleConfirm}>
                                    <Link to="/product" data-bs-toggle="modal" data-bs-target="#self-transport-modal" className="btn btn-success w-100">Confirm</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
            <div className="modal" id="self-transport-modal" tabIndex="-1">
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content">
                        <div className="modal-body px-4 pb-4">
                            <div className="top-right">
                                <Link to="/product">
                                <button type="button" className="btn-close size-50px" data-bs-dismiss="modal" 
                                aria-label="Close"></button>
                                </Link>
                            </div>
                            <div className="my-3 text-center">
                                <h6 className="fw-medium text-danger my-3 line-heading">Order confirmed</h6>

                                 <div className="container text-success ">
                                    <h4>Your Order Id: {order_id}</h4>
                                    <p>Name: {userDetails.name}</p>
                                    <p>Phone: {userDetails.phone}</p>
                                    <p>Email: {userDetails.email}</p>
                                    <p>Total: ₹{userDetails.total_amount}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="container d-md-none d-block">
                <div className="row">
                    <div className="col-md-5 mx-auto pt-5">
                        <div className="d-flex justify-content-center">
                            <ul className="list-unstyled d-flex dots">
                                <li className="dots-place-order"></li>
                                <li className="dots-place-order"></li>
                                <li className="dots-place-order active"></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
