import React from 'react'
import Haldiram from '../assets/images/haldirams.png';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
 import { useEffect, useState } from 'react';
 import CartItem  from './CartItem';
import { saveOrderProduct } from '../services/actions/productAction';
 
 const Cart = () => {
  
const cart =useSelector((state)=>state.allProducts.cart);
 const [totalPrice , setTotalPrice]=useState(0);
 const [totalItem , setTotalitem] =useState(0);
const dispatch = useDispatch();
 let price=0;
 let quantity =0;
 let product_id ="";
 let orderProduct = [];
 
 cart.forEach((item)=>{
   product_id=item.id;
   quantity =item.qty;
   orderProduct.push({product_id , quantity})
 })
 dispatch(saveOrderProduct(orderProduct));

 useEffect(()=>{

   cart.forEach(item => {
     price +=item.price *item.qty;
     quantity = item.qty+1;
   });
   setTotalPrice(price);
   setTotalitem(quantity)
 },[cart , totalPrice ,setTotalPrice , totalItem,setTotalitem ]);

    return (
        <>
           <section>
      <div   className="cart-details">
        <div   className="container px-0">
          <div   className="col-md-8 mx-auto">
            <div   className="row pb-lg-4 pb-0">
              <div   className="col-lg-3 col-sm-3 col-5">
                <img   className="img-fluid" src= {Haldiram} alt=""/>
              </div>
              <div   className="col-lg-9 col-sm-7 col-7">
                <h3   className="title-heading">Haldiram's</h3>
                <p   className="mb-0 sub-title">54/01 XYZ Street, Sector 41</p>
                <address   className="sub-title">ABC City | 4 kms</address>
              </div>
            </div>
            
            <div   className="row">
              <div   className="col-lg-8 my-2">
                <div   className="shadow-sm p-3">        
                  {cart.map((itm ,index)=>{
                    return(
                    <CartItem key={itm.id} itemData={itm}  />
                    )
                //  return(
      
                  // <div   className=" d-flex justify-content-between align-items-center">
                  //   <div>
                  //     <h4   className="pro-text">{itm.name}</h4>
                  //     <p   className="pro-price">₹ { itm.price * itm.qty}</p>
                      
                  //   </div>
              
                  //   <div>
                  //     <div   className="control product_qty">                                             
                  //       <input type="button"    className="btn bttn-left btn-outline-light border-end-0" onclick="{decrementValue}" value="-" />
                  //       <input type="text"   className="input  border border-start-0 border-end-0" name="quantity" value={itm.qty}  maxlength="2" max="10" size="1" id="number" />
                  //    <div onclick="myAlertBottom()"   className="d-inline-flex">
                  //       <input type="button"   className="btn bttn-right btn-outline-light border-start-0" onclick="{incrementValue} " value="+" />
                  //    </div>
                  //    </div>
                  //   </div>
                  // </div>
                    //  )   
                  })}                 
                </div>

                <div   className="shadow-sm p-3">
                  <div   className=" d-flex justify-content-between align-items-center">
                    <div   className="price-details-title">
                      <div   className="d-flex align-items-center">
                        <img   className="object-fit-contain" src="assets/images/location-icon.png" alt=""/>
                        <div   className="px-2">
                          <p   className="items mb-0 fw-bold">
                            Dine In
                          </p>
                          <p   className="mb-0">Table Number:- 23</p>
                        </div>
                      </div>
                    </div>
                   <div>
                      <a href="fill-details.html"   className="text-uppercase text-decoration-none text-danger">EDIT</a>
                    </div>   
                  </div>
                </div>
              </div>
              <div   className="col-lg-4 price-details my-2">
                <div   className="shadow-sm p-4">
                  <h4   className="price-details-title pb-2">Bill Details</h4>
                  <div   className="d-flex justify-content-between align-items-center border-bottom pb-1">
                    <div>
                      <h4   className="items">Total price</h4>
                    </div>
                    <div>
                      <p   className="price mb-0">
                        {/* ₹400.00 */}
                        {totalPrice}
                        </p>
                    </div>
                  </div>
                  <div   className="d-flex justify-content-between align-items-center pt-4">
                    <div>
                      <h4   className="items text-success fw-medium">To Pay</h4>
                    </div>
                    <div>
                      <p   className="price mb-0 fw-bold">₹ {totalPrice}</p>
                    </div>
                  </div>
                </div>
                <div   className="pt-3">
                  <Link to={totalPrice>0 ?"details" :"/product"}  className="btn btn-success w-100">Order Now</Link>
                </div>
                <div   className="pt-3 d-md-none d-block">
                  <Link to="/product"   className="btn btn-success w-100">Add More Dishes</Link>
                </div>
              </div>
            </div>
         
          </div>
        </div>
      </div>
    </section>
  <div   className="container d-md-none d-block">
    <div   className="row">
      <div   className="col-md-5 mx-auto pt-3">
        <div   className="d-flex justify-content-center">
          <ul   className="list-unstyled d-flex dots">
            <li   className="dots-place-order active"></li>
            <li   className="dots-place-order"></li>
            <li   className="dots-place-order"></li>
          </ul>
        </div>
      </div>
    </div>
    </div>
        </>
    )
}
export default Cart;