import React  from 'react'
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { useEffect } from 'react';
import axios from 'axios';
import { ProductItem } from './productCategory/ProductItem';
import { setCusines } from '../../services/actions/productAction';

export const DishesCategory = () => {

    const cusionData = useSelector((state)=>state.allProducts.cusine)
   const dispatch = useDispatch();
  const url = "http://139.59.13.212:1267/products/all/?resturantid=1";

  useEffect(() => {
    async function fetchPopularProducts() {
      const response = await axios.get(url);
      //console.log("Cusion Response is ",response.data.data.cusine);
      dispatch(setCusines(response.data.data.cusine));
    }
    fetchPopularProducts();
  }, [dispatch]);
   
   //console.log("cusion data ", cusionData);
   return (
      <>
         <section className="pt-0 dishes-details px-lg-5 px-0">
            <div className="container px-0">
             
                <div className=" row mx-0 accordion accordion-flush">

                  {cusionData?.map((cusionproduct, index) => {
                     return (
                        <div className="col-xl-4 col-lg-6" key={cusionproduct.id}>
                           <div className="accordion-item" id={index}  >
                            
                              <h2 className="accordion-header" id={`flush-heading${index + 1}`}>
                                 <button className="accordion-button px-2 border" type="button" data-bs-toggle="collapse"
                                    data-bs-target={`#flush-collapse${index + 1}`} aria-expanded="true" aria-controls={`flush-collapse${index + 1}`}>
                                    {cusionproduct.description}
                                 </button>
                              </h2>  
                              


                              <div id={`flush-collapse${index + 1}`} className="accordion-collapse collapse show" aria-labelledby={`flush-heading${index + 1}`}
                                 data-bs-parent="#accordionFlushExample1">

                          <ProductItem productData={cusionproduct}/> 

                              </div>
                           </div>
                        </div>
                     );
                  })}
               </div> 
            </div>   
         </section>
      </>
   )
}
