import React, { useState } from 'react';
import {  useDispatch } from 'react-redux';
import { Alert } from '../../../alert/Alert';
 import greenicon from '../../../assets/images/green-icon.png';
import { addToCart } from '../../../services/actions/productAction';
 import IncrementDecrement from './../IncrementDecrement';
 
export const Item = ({ img, name, price, id, desc }) => {

    const [cartFlag, updateCartFlag] = useState(true);
    const dispatch = useDispatch();
    const update = () => {
        updateCartFlag(false);
    }; 
    return (
        <>
            <div className="shadow-sm py-2  px-2 position-relative">
                <div className="row g-1">
                    <div className="col-3">
                        <img className="img-fluid" src={img} alt="pro-2" />
                    </div>
                    <div className="col-9">
                        <div className="border-bottom">
                            <p className="mb-2 pro-title"><span><img src={greenicon}
                                alt="green-icon" /></span>
                                {name}</p>
                            <p className="pro-info mb-1">
                                {desc.substring(0,100)}
                            </p>
                        </div>
                        <div className="d-flex justify-content-between align-self-center pt-2">
                            <div>
                                <ul className="ps-0 d-flex pt-1 mb-0">
                                    <li className="ms-4 reting">₹ {price}</li>
                                </ul>
                            </div>
                            <div onClick={() => {
                                update(); 
                            }}>
                                {cartFlag ? (<span
                                    className="btn btn-success px-2 py-1 btn-success-add fw-bold add-none custom-d-block"
                                    onClick={() => {
                                        dispatch(addToCart(id));
                                         <Alert/>;
                                    }}
                                    >
                                    ADD
                                </span>
                                ) : (
                                    <IncrementDecrement id={id} />
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
