// import { Data } from '@react-google-maps/api'
import * as actionTypes from '../constants/shopping-types'

//ProductsReducer

export const setProducts = (products) => {
  return {
    type: actionTypes.SET_PRODUCTS,
    payload: products,
  };
};
export const setCusines = (cusine) => {
  return {
    type: actionTypes.SET_CUSINE,
    payload: cusine,
  };
};
export const setPopularProducts = (popularProducts) => {
  return {
    type: actionTypes.SET_POPULAR_PRODUCTS,
    payload: popularProducts,
  };
};

//CartReducer

export const addToCart = (itemID) => {
  return {
    type: actionTypes.ADD_TO_CART,
    payload: {
      id: itemID,
    },
  };
};

export const removeFromCart = (itemID) => {
  return {
    type: actionTypes.REMOVE_FROM_CART,
    payload: {
      id: itemID,
    },
  };
};

export const adjustQty = (itemID, value) => {
  return {
    type: actionTypes.ADJUST_QTY,
    payload: {
      id: itemID,
      qty: value,
    },
  };
};

export const loadCurrentItem = (item) => {
  return {
    type: actionTypes.LOAD_CURRENT_ITEM,
    payload: item,
  };
};

export const clearCart =()=>{
  return{
    type : actionTypes.CLEAR_CART,
  }
}

// product reducer
export const saveOrderProduct =(item)=>{
  return{
    type :actionTypes.SET_ORDER_PRODUCT,
    payload : item,
  }
}

export const saveUserDetails =(details)=>{
  return{
    type :actionTypes.SET_USER_DETAILS,
    payload :details,
  }
}
 
export const createOrder =(orderProdduct , userDetal)=>{

  return{
    type :actionTypes.CREATE_ORDER,
    payload : {
      orderProdducts : orderProdduct,
      userdetals : userDetal
    }
  }
}
export const clearOrder =()=>{
  return{
    type :actionTypes.CLEAR_ORDER,
  }
}

