import React from 'react'
import { Search } from './Search'
import pro from '../../assets/images/pro.jpg'
export const Banner = () => {
    return (
        <>
            <section className="banner-dishes py-4">
                <div className="container px-xl-5 px-0">
                    <div className="row mx-0">
                        <div className="col-md-3 pt-3 d-md-block d-none">
                            <img className="img-fluid pro-img rounded-3" src=  {pro} alt="pro-1" />
                        </div>
                        <div className="col-md-9 align-self-center d-md-block d-none">
                            <div className="dishes-title">

                            </div>
                            <div className="location-text">
                                Noida Expressway, Noida Expressway
                            </div>
                            <ul className="ps-0 d-flex pt-4 mb-0">
                                <li className="list-unstyled reting"><i className="fas fa-map-marker-alt pe-2"></i>54/01 XYZ Street</li>
                                <li className="ms-4 reting">₹200 </li>
                            </ul>
                        </div>
                        <Search/>



                    </div>
                </div>
            </section>
        </>
    )
}
